.nav-tabs {
    .slick-list {
        li {
            &.slick-current {
                a.active {
                    color: $primary-color;
                }
            }
        }
    }
}

.grand_menu_container {
    @include breakpoint (xs) {
        padding: 0;
    }
    .mina_all_menu {
        @include breakpoint (xs) {
            text-align: left;
            padding: 0 15px;
        }
    }
}

#_mina_grand_menu {
    .mina_menu_tabs {
        ul.nav-tabs {
            @include breakpoint (xs) {
                @include border-radius(0px);
            }
        }
    }
}

.spicy-labels {
    .row {
        align-items: flex-end;
    }
    padding-left: 20px;
    label {
        display: inline-block;
        font-size: 16px;
    }
    .sp-quantity {
        display: flex;
        .input-qty {
            cursor: pointer;
        }
        .input-qty,
        .quntity-input {
            height: 38px;
            width: 38px;
            border: 1px solid #ccc;
            outline: none;
            box-shadow: none;
            background: $white;
            display: inline-block;
            text-align: center;
            color: $black;
            line-height: 38px;
            font-size: 16px;
            text-decoration: none;
            @include border-radius(0);
            padding: 0;
        }
        .quntity-input {
            border-left: none;
            border-right: none;
        }
    }
    .info {
        background: $primary-color;
        color: $white;
        height: 20px;
        width: 20px;
        display: inline-block;
        text-align: center;
        line-height: 23px;
        border-radius: 50%;
        font-size: 13px;
        vertical-align: middle;
        font-weight: 600;
        cursor: pointer;
    }
    .form-group {
        margin-bottom: 0;
    }
    select {
        &.form-control {
            // border-radius: 0;
            @include border-radius (0);
            box-shadow: none;
            outline: none;
            border: 1px solid #ccc;
            max-width: 140px;
        }
    }
    .add_to_takeout_btn {
        .btn {
            &--primary {
                margin-left: 0;
            }
        }
    }
}

.tab-content {
    .mina_item_content {
        .item_title_price {
            min-height: 77px;
            &.has__multiprice {
                .price-level {
                    margin-bottom: 15px;
                    span.price {
                        /* margin-left: 50px; */
                        font-size: 19px;
                        font-weight: 700;
                        margin-right: 5px;
                        margin-left: 0;
                        span.item_tax_label {
                            font-size: 12px;
                        }
                    }
                }
            }
        }
    }
}

#modal-spiecy-level {
    .modal-dialog {
        max-width: 800px;
        .modal-content {
            border: none;
            .modal-body {
                // background: $primary-color;
            }
            table {
                background: linear-gradient(rgba(217, 11, 23, 0), rgba(202, 12, 23, 0.48), rgba(142, 6, 14, 0.83));
                tbody {
                    tr {
                        td {
                            &:first-child {
                                font-weight: 700;
                            }
                        }
                    }
                }
            }
        }
    }
    .close {
        font-weight: 400;
    }
    .table-bordered {
        th,
        td {
            border: 1px solid $black;
        }
    }
}

.slider-nav {
    .slick-arrow {
        height: 30px;
        width: 30px;
        text-align: center;
        line-height: 30px;
        font-size: 20px;
        position: absolute;
        top: 12px;
        // transform: translateY(-50%);
        cursor: pointer;
        &.next {
            right: 20px;
        }
        &.prev {
            left: 20px;
        }
        &.slick-disabled {
            opacity: 0;
            pointer-events: none;
        }
    }
}