.ft-address-block {
    margin-bottom: 40px;
    @include breakpoint (xs) {
        margin-bottom: 0;
    }
}

#mina_footer {
    .sub-branches {
        h3 {
            margin-bottom: 20px;
        }
        .ft-address-block {
            h4 {
                margin-top: 0;
            }
        }
    }
}

.ft-icons {
    @include breakpoint (xs) {}
    margin-bottom: 30px;
    ul {
        margin: 0;
        padding: 0;
        li {
            list-style: none;
            a {
                transition: all 0.5s;
                &:not(:hover) {
                    color: $white !important;
                }
            }
        }
    }
}